*
{
    margin: 0;
    padding: 0;
    overflow-x: hidden;

}

html
{
    background: #FCFCFC;
    position: flex;
    max-width: 100vw;
    height: 100vh;
}

.webgl
{
    position: fixed;
    padding: 0;
    top: 0;
    left: 0;
    outline: none;
    width: auto;
    height: auto;
    overflow: hidden;
}

.header
{
    /* header */


    /* Auto layout */

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    padding: 20px;
    /* gap: 20vh; */

    width: 100vw;
    height: 10vh;


    /* Inside auto layout */

    flex: none;
    order: 0;
    flex-grow: 0;
}

.logocomplete
{
    /* Auto layout */

    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px;
    gap: 2.5vw;

    width: auto;
    height: auto;


    /* Inside auto layout */

    flex: none;
    order: 0;
    flex-grow: 0;
}

.logoblob
{
    /* logotext */
    display: flex;
    flex-direction: column;
    /* align-items: left; */

    width: auto;
    height: auto;


    /* Inside auto layout */

    flex: none;
    order: 1;
    flex-grow: 0;
}

.logoplace
{
    /* Angell-Logo Position */

    width: auto;
    height: auto;
    
    
    /* Inside auto layout */
    
    flex: none;
    order: 0;
    flex-grow: 0;
}

.logotexta
{
    position: flex;
    width: auto;
    height: auto;
    
    font-family: 'DM Mono';
    font-style: normal;
    font-weight: 600;
    font-size: 4.5vh;
    /* line-height: 83px; */
    
    color: #040304;

    flex: none;
    order: 0;
    flex-grow: 0;
}

.logotextb
{
    /* Tagline */
    position: flex;
    width: auto;
    height: auto;

    font-family: 'Roboto';
    font-style: normal;
    font-weight: 300;
    font-size: 2.75vh;
    /* line-height: auto; */
    /* identical to box height */

    text-align: center;

    color: #040304;

    flex: none;
    order: 1;
    flex-grow: 0;
}

.blob
{
    display: flex;
    flex-direction: column;
    justify-content: center;

    /* padding: 23px; */
    gap: 2vh;

    width: 65vw;
    height: 80vh;
}


.h2
{
    width: auto;
    height: auto;
    
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 800;
    font-size: 4vh;
    
    color: #040304;
    
    
    /* Inside auto layout */
    
    flex: none;
    order: 0;
    flex-grow: 0;
}

.p
{
    width: 55vw;
    height: auto;
    
    font-family: 'DM Mono';
    font-style: normal;
    font-weight: 400;
    font-size: 2.50vh;
    
    color: #040304;
    
    
    /* Inside auto layout */
    
    flex: none;
    order: 1;
    flex-grow: 0;
    
}


.section
{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    position: left;
    color: #040304;
    padding-left: 6vw;

    width: 100vw;
    height: 100vh;

}

.sectionb
{

    display: flex;
    flex-direction: row-reverse;
    /* justify-content: right; */
    align-items: flex-start;
    color: #040304;

    width: 100vw;
    height: 100vh;

    /* font-size: 3vmin; */

}

